#steps > li {
  display: block;
  height: 4em;
  padding: 0.5em;
  margin-bottom: 1em; }

#steps > li:nth-child(1) {
  margin-left: calc((100vw - 4em * 2) * (1 - 1) / 8); }

#steps > li:nth-child(1) > img {
  margin-right: 1em; }

#steps > li:nth-child(2) {
  margin-left: calc((100vw - 4em * 2) * (2 - 1) / 8); }

#steps > li:nth-child(2) > img {
  margin-right: 1em; }

#steps > li:nth-child(3) {
  margin-left: calc((100vw - 4em * 2) * (3 - 1) / 8); }

#steps > li:nth-child(3) > img {
