#steps > li:nth-child(4) > img {
  margin-right: 1em; }

#steps > li:nth-child(5) {
  margin-right: calc((100vw - 4em * 2) * (8 - 5) / 8);
  float: right; }

#steps > li:nth-child(5) > img {
  margin-left: 1em; }

#steps > li:nth-child(6) {
  margin-right: calc((100vw - 4em * 2) * (8 - 6) / 8);
  float: right; }

#steps > li:nth-child(6) > img {
  margin-left: 1em; }

#steps > li:nth-child(7) {
  margin-right: calc((100vw - 4em * 2) * (8 - 7) / 8);
  float: right; }

#steps > li:nth-child(7) > img {
  margin-left: 1em; }

#steps > li:nth-child(8) {
  margin-right: calc((100vw - 4em * 2) * (8 - 8) / 8);
  float: right; }

#steps > li:nth-child(8) > img {
  margin-left: 1em; }

#steps > li:nth-child(9) {
  margin-right: calc((100vw - 4em * 2) * (8 - 9) / 8);
