#steps img {
  border-radius: 2em;
  border: 0.1em solid black;
  width: 4em;
  height: 4em; }

img.ipython {
  max-width: 100%;
  width: auto \9;
  height: auto;
  vertical-align: middle;
  border: 0;
  -ms-interpolation-mode: bicubic; }

.container {
  width: 940px;
  margin-right: auto;
  margin-left: auto;
  *zoom: 1; }

.container:before, .container:after {
  display: table;
  content: "";
  line-height: 0; }

.container:after {
  clear: both; }

pre.ipython {
  font-family: monospace;
  color: #333;
  display: block;
  padding: 9.5px;
  margin: 0 0 10px;
  word-break: normal;
  word-wrap: normal;
  background-color: #f5f5f5;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  font-size: inherit;
  line-height: inherit;
  white-space: pre; }

@media (min-width: 1200px) {
  .container {
    width: 1170px; } }

@media (min-width: 768px) and (max-width: 979px) {
  .container {
    width: 724px; } }

@media (max-width: 767px) {
  .container {
    width: auto; } }

@media (max-width: 979px) {
  body {
    padding-top: 0; } }

.border-box-sizing {
  box-sizing: border-box; }

.ansigreen {
  color: #006400; }

.ansipurple {
  color: #9400d3; }

.ansicyan {
  color: #4682b4; }

div.cell {
