.countdown-container {
  width: 52.22222222vw;
  background: #d9534f;
  margin: 0 auto 0 auto;
  position: relative;
  display: flex;
  flex-direction: row;
  -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(50%, transparent), to(rgba(255, 255, 255, 0.25))); }

.countdown-part-container {
  display: flex;
  flex-direction: row; }
  .countdown-part-container::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    z-index: 10;
    width: 15.55555556vw;
    height: 4vh;
    background-image: linear-gradient(to bottom, #d9534f 0%, rgba(217, 83, 79, 0) 100%); }

.countdown-delimiter {
  color: black;
  font-size: 10vw;
  line-height: 1.0;
  font-family: serif;
  width: 2.77777778vw;
  height: 10vw;
  position: relative; }

.countdown-delimiter > span {
  display: inline-block;
  position: absolute;
  bottom: 0.08em; }

.digit {
  display: block;
  color: #d9534f;
  height: 10vw;
  width: 10vw;
  margin-left: -1.11111111vw; }

.countdown-part {
  width: 7.77777778vw;
  height: 10vw;
  overflow: hidden;
  box-sizing: border-box; }

.countdown-part.hours.tens .digit-wrapper {
  animation: hours-tens 86400s cubic-bezier(1, 0, 1, 0) 0s 1; }
.countdown-part.hours.ones .digit-wrapper {
  animation: ones 36000s cubic-bezier(1, 0, 1, 0) -21600s 3; }
.countdown-part.minutes.tens .digit-wrapper {
  animation: minsec-tens 3600s cubic-bezier(1, 0, 1, 0) 0s 24; }
.countdown-part.minutes.ones .digit-wrapper {
  animation: ones 600s cubic-bezier(1, 0, 1, 0) 0s 144; }
.countdown-part.seconds.tens .digit-wrapper {
  animation: minsec-tens 60s cubic-bezier(1, 0, 1, 0) 0s 1440; }
.countdown-part.seconds.ones .digit-wrapper {
  animation: ones 10s cubic-bezier(1, 0, 1, 0) 0s 8640; }

@keyframes hours-tens {
  0% {
    transform: translateY(-10vw); }

  100% {
    transform: translateY(-10vw); }

  16.66666667% {
    transform: translateY(-20vw); }

  58.33333333% {
    transform: translateY(-30vw); }

  58.33334333% {
    transform: translateY(0px); } }

@keyframes minsec-tens {
  0% {
    transform: translateY(-10vw); }

  100% {
    transform: translateY(-10vw); }

  16.66666667% {
    transform: translateY(-20vw); }

  33.33333333% {
    transform: translateY(-30vw); }

  50% {
    transform: translateY(-40vw); }

  66.66666667% {
    transform: translateY(-50vw); }

  83.33333333% {
    transform: translateY(-60vw); }

  83.33334333% {
    transform: translateY(0px); } }

@keyframes ones {
  0% {
    transform: translateY(-10vw); }

  100% {
    transform: translateY(-10vw); }

  10% {
    transform: translateY(-20vw); }

  20% {
    transform: translateY(-30vw); }

  30% {
    transform: translateY(-40vw); }

  40% {
    transform: translateY(-50vw); }

  50% {
    transform: translateY(-60vw); }

  60% {
    transform: translateY(-70vw); }

  70% {
    transform: translateY(-80vw); }

  80% {
    transform: translateY(-90vw); }

  90% {
    transform: translateY(-100vw); }

  90.00001% {
    transform: translateY(0px); } }
